<template>
  <div id="collection-share-dialog-component">
    <el-dialog
      title="Management Users"
      v-model="visible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <div class="select-item flex">
          <p>Owner: Sam</p>
          <p class="copy" @click="onCopy">Copy Link</p>
        </div>
        <div class="select-item">
          <p>Users:</p>
          <el-select v-model="selectValue" filterable placeholder="Select">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.checked"
            >
            </el-option>
          </el-select>
        </div>
        <div class="tag-container" v-if="tags.length">
          <FilterTag
            v-for="tag in tags"
            :key="tag.value"
            :data="tag.label"
            @close="onTagCloseClick(tag.value)"
          ></FilterTag>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, watch } from "vue";
import { useStore } from "vuex";

import FilterTag from "@/components/FilterTag.vue";
import { OptionItem, OptionItemResponse } from "@/typings/asideFilter";
import {
  GetAvailalbeMembers,
  GetJoinedMembers,
  PostUpdateJoinedMembers
} from "@/typings/store";
import {
  AvailableMember,
  CollectionUpdateJoinedMemberParams,
  JoinedMember,
  User
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { copyToClipboard } from "@/utils";

const Component = defineComponent({
  name: "ShareDialog",
  components: {
    FilterTag
  },
  setup() {
    const store = useStore();
    const collectionId = ref(0);
    const visible = ref(false);
    const selectValue = ref("");
    const lock = ref(false);

    const tags = computed(() => {
      return store.state.collection.joinedMembers.reduce(
        (list: OptionItemResponse[], company: JoinedMember) => {
          const userList = company.users.reduce(
            (_list: OptionItemResponse[], user: User) => {
              const item: OptionItemResponse = {
                label: company.company_name + "-" + user.username,
                value: `${user.id}`
              };
              return [..._list, item];
            },
            []
          );
          return [...list, ...userList];
        },
        []
      );
    });
    const options = computed<OptionItem>(() => {
      return store.state.collection.availableMembers.map(
        (item: AvailableMember) => ({
          label: item.username,
          value: item.member_id,
          checked: false
        })
      );
    });

    const init = async (id: number) => {
      await store.dispatch(GetAvailalbeMembers, { id, q: "" });
      await store.dispatch(GetJoinedMembers, id);
    };

    watch(selectValue, async id => {
      if (!id || typeof id !== "number") return;
      try {
        const params: CollectionUpdateJoinedMemberParams = {
          collection_id: collectionId.value,
          member_id: id
        };
        await store.dispatch(PostUpdateJoinedMembers, params);
        await init(collectionId.value);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
      selectValue.value = "";
    });

    const open = async (id: number) => {
      collectionId.value = id;
      visible.value = true;
      await init(id);
    };

    const onTagCloseClick = async (id: number) => {
      if (lock.value) return;
      lock.value = true;
      try {
        const params: CollectionUpdateJoinedMemberParams = {
          collection_id: collectionId.value,
          member_id: id
        };
        await store.dispatch(PostUpdateJoinedMembers, params);
        await init(collectionId.value);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      } finally {
        lock.value = false;
      }
    };

    const onCopy = () => {
      const href = window.location.href;
      copyToClipboard(href);
      ElNotification({
        title: "Success",
        type: "success",
        message: `Your Copied Link ${href}`
      });
    };

    return {
      tags,
      options,
      visible,
      selectValue,

      open,
      onTagCloseClick,
      onCopy
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#collection-share-dialog-component
  .select-item
    margin-bottom 30px
    p
      margin 10px 0
      font-size 16px
      font-weight 600

    :deep() .el-select
      width 100%
  .flex
    padding-right 30px
    flex(space-between)
    .copy
      cursor pointer
      border-bottom 1px solid $color-secondary
  .tag-container
    margin-top 20px
</style>
