<template>
  <div id="collection-create-dialog-component">
    <el-dialog
      :title="`${editable ? 'Edit' : 'Create'} Collection`"
      v-model="visible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <el-row>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Privacy</p>
                <el-select v-model="form.status">
                  <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="2"></el-col>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Show Label</p>
                <el-select v-model="form.show_label">
                  <el-option
                    v-for="item in showLabelOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Collection Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Description (optional)</p>
            <el-input v-model="form.description" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <el-row :gutter="40">
              <el-col class="nmb" :span="12">
                <p class="label">Year (optional)</p>
                <el-select v-model="form.year">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="nmb" :span="12">
                <p class="label">Season (optional)</p>
                <el-select v-model="form.season">
                  <el-option
                    v-for="item in seasonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Theme (optional)</p>
            <el-input v-model="form.theme" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Hashtag (optional)</p>
            <el-input v-model="form.hash_tag" clearable> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="visible = false">Cancel</el-button>
          <el-button type="success" @click="onConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, reactive } from "vue";
import { useStore } from "vuex";

import {
  CollectionCreateInputs,
  CollectionDetailResponse
} from "@/typings/collection";
import { PostCreateCollection, PostUpdateCollection } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";

const Component = defineComponent({
  name: "CreateDialog",
  props: {
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const store = useStore();
    const { editable } = toRefs(props);

    const visible = ref(false);
    const form = reactive<CollectionCreateInputs>({
      id: 0,
      status: "private",
      show_label: false,
      name: "",
      owner: "",
      description: "",
      year: 0,
      season: "",
      theme: "",
      hash_tag: ""
    });

    const create = async () => {
      const params: any = {
        name: form.name,
        description: form.description,
        season: form.season,
        show_label: form.show_label,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        // TODO remove creator when api fixed.
        owner: 4,
        creator: 4,
        sport_type: []
      };
      if (form.year) params.year = form.year;
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const edit = async () => {
      const params: any = {
        name: form.name,
        description: form.description,
        season: form.season,
        show_label: form.show_label,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        // TODO remove creator when api fixed.
        owner: 4,
        creator: 4,
        sport_type: []
      };
      if (form.year) params.year = form.year;
      await store.dispatch(PostUpdateCollection, {
        id: form.id,
        params
      });
      visible.value = false;
      emit("load");
    };

    const open = (data?: CollectionDetailResponse) => {
      visible.value = true;
      if (editable.value && data) {
        form.id = data.id;
        form.status = data.status;
        form.show_label = data.show_label;
        form.name = data.name;
        form.description = data.description;
        form.year = data.year;
        form.season = data.season;
        form.theme = data.theme;
        form.hash_tag = data.hash_tag;
      }
    };

    const onConfirm = async () => {
      if (editable.value) {
        await edit();
      } else {
        await create();
      }
    };

    return {
      form,
      visible,
      statusOptions,
      showLabelOptions,
      yearOptions,
      seasonOptions,

      open,
      onConfirm
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#collection-create-dialog-component
  .inputs-container
    padding 0 10%
    .label
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px
      margin-bottom 15px

    .nmb
      margin-bottom 0 !important

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-select
      width 100%
</style>
