<template>
  <div id="collection-create-project-dialog-component">
    <el-dialog
      title="Create Project"
      v-model="visible"
      :width="step === 1 ? '80%' : '40%'"
      top="45px"
    >
      <!--Step 1-->
      <div class="step-1-container" v-if="step === 1">
        <div class="input-item">
          <p class="label">Project Name:</p>
          <el-input v-model="form.name" clearable> </el-input>
        </div>
        <el-row :gutter="60" v-if="detail">
          <el-col
            v-for="item in detail.collection_fabrics"
            :key="item.fabric.id"
            :span="6"
          >
            <FabricCardSelectable
              :data="item.fabric"
              :selected="selectedId === item.fabric.id"
              @select="onSelect"
            ></FabricCardSelectable>
          </el-col>
        </el-row>
      </div>

      <!--Step 2-->
      <div class="step-2-container" v-if="step === 2">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Expected Finished Time</p>
            <el-date-picker v-model="form.time" placeholder="選擇日期">
            </el-date-picker>
          </el-col>
          <el-col :span="24">
            <p class="label">Members</p>
            <el-select v-model="form.members">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <div class="tag-container" v-if="tags.length">
              <FilterTag
                v-for="tag in tags"
                :key="tag.value"
                :data="tag.label"
                @close="onTagCloseClick(tag.value)"
              ></FilterTag>
            </div>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="step === 1" type="info" @click="visible = false">
            Cancel
          </el-button>
          <el-button v-if="step === 2" type="info" @click="step = 1">
            Back
          </el-button>
          <el-button v-if="step === 1" type="success" @click="step = 2">
            Next
          </el-button>
          <el-button v-if="step === 2" type="success" @click="onDoneClick">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, watch, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import FilterTag from "@/components/FilterTag.vue";
import {
  GetAvailalbeMembers,
  GetCollection,
  GetJoinedMembers
} from "@/typings/store";
import { ElNotification } from "element-plus";
import { OptionItem, OptionItemResponse } from "@/typings/asideFilter";
import { useRouter } from "vue-router";
import { FabricCardSelectableEmit } from "@/typings/fabric";
import { AvailableMember, JoinedMember, User } from "@/typings/collection";

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCardSelectable,
    FilterTag
  },
  setup() {
    const store = useStore();
    const router = useRouter();

    const visible = ref(false);
    const collectionId = ref(-1);
    const selectedId = ref(-1);
    const selectValue = ref("");
    const lock = ref(false);
    const form = reactive({
      fabrics: [],
      name: "",
      time: "",
      members: [],
      brands: []
    });
    const step = ref(1);

    const detail = computed(() => store.state.collection.detail);

    const tags = computed(() => {
      return store.state.collection.joinedMembers.reduce(
        (list: OptionItem[], company: JoinedMember) => {
          const userList = company.users.reduce(
            (_list: OptionItem[], user: User) => {
              const item: OptionItem = {
                label: company.company_name + "-" + user.username,
                value: `${user.id}`,
                checked: false
              };
              return [..._list, item];
            },
            []
          );
          return [...list, ...userList];
        },
        []
      );
    });
    const options = computed<OptionItem>(() => {
      return store.state.collection.availableMembers.map(
        (item: AvailableMember) => ({
          label: item.username,
          value: item.member_id,
          checked: false
        })
      );
    });

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);
      await store.dispatch(GetAvailalbeMembers, { id, q: "" });
      await store.dispatch(GetJoinedMembers, id);
    };

    const open = async (_id: number) => {
      step.value = 1;
      collectionId.value = _id;
      visible.value = true;
      await init(_id);
    };

    watch(selectValue, async id => {
      if (!id || typeof id !== "number") return;
      try {
        console.log("work");
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
      selectValue.value = "";
    });

    const onDoneClick = async () => {
      try {
        await router.push({ name: "ProjectDetail", params: { id: 1 } });
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${form.name} Failed.`
        });
      }
      visible.value = false;
    };

    const onSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      selectedId.value = data.id;
    };

    const onTagCloseClick = async (id: number) => {
      console.log(id);
      // if (lock.value) return;
      // lock.value = true;
      // lock.value = false;
    };

    return {
      form,
      detail,
      visible,
      selectValue,
      step,
      tags,
      options,
      selectedId,

      open,
      onDoneClick,
      onSelect,
      onTagCloseClick
    };
  }
});

export default Component;
</script>

