      font-weight 500
      padding 0 8px
      margin-bottom 15px

    .tag-container
      margin-top 20px

    .nmb
      margin-bottom 0 !important

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-select
      width 100%

