
import { defineComponent, ref, toRefs, reactive } from "vue";
import { useStore } from "vuex";

import {
  CollectionCreateInputs,
  CollectionDetailResponse
} from "@/typings/collection";
import { PostCreateCollection, PostUpdateCollection } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";

const Component = defineComponent({
  name: "CreateDialog",
  props: {
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const store = useStore();
    const { editable } = toRefs(props);

    const visible = ref(false);
    const form = reactive<CollectionCreateInputs>({
      id: 0,
      status: "private",
      show_label: false,
      name: "",
      owner: "",
      description: "",
      year: 0,
      season: "",
      theme: "",
      hash_tag: ""
    });

    const create = async () => {
      const params: any = {
        name: form.name,
        description: form.description,
        season: form.season,
        show_label: form.show_label,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        // TODO remove creator when api fixed.
        owner: 4,
        creator: 4,
        sport_type: []
      };
      if (form.year) params.year = form.year;
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const edit = async () => {
      const params: any = {
        name: form.name,
        description: form.description,
        season: form.season,
        show_label: form.show_label,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        // TODO remove creator when api fixed.
        owner: 4,
        creator: 4,
        sport_type: []
      };
      if (form.year) params.year = form.year;
      await store.dispatch(PostUpdateCollection, {
        id: form.id,
        params
      });
      visible.value = false;
      emit("load");
    };

    const open = (data?: CollectionDetailResponse) => {
      visible.value = true;
      if (editable.value && data) {
        form.id = data.id;
        form.status = data.status;
        form.show_label = data.show_label;
        form.name = data.name;
        form.description = data.description;
        form.year = data.year;
        form.season = data.season;
        form.theme = data.theme;
        form.hash_tag = data.hash_tag;
      }
    };

    const onConfirm = async () => {
      if (editable.value) {
        await edit();
      } else {
        await create();
      }
    };

    return {
      form,
      visible,
      statusOptions,
      showLabelOptions,
      yearOptions,
      seasonOptions,

      open,
      onConfirm
    };
  }
});

export default Component;
